<template>
	<view>
		<view class="title">{{bill.title}}</view>
		<view class="status" v-if="bill.status == 1">状态:未支付</view>
		<view class="status" v-if="bill.status == 2">状态:已支付</view>
		<rich-text :nodes="bill.content"></rich-text>
		<view class="price">费用:{{bill.price}}</view>
		<view class="pay">
			<u-button size="mini" @click="viewPayType" v-if="bill.status == 1">支付</u-button>
		</view>
		<u-popup v-model="showPay" mode="bottom">
			<view class="pay_info">
				<view class="price">￥{{bill.price}}</view>
				<view>选择支付方式</view>
				<u-radio-group v-model="value">
					<u-radio @change="radioChange" v-for="(v,k) in payInfo" :name="v.type" :key="k"  :disabled="v.disabled">
						{{v.name}}
					</u-radio>
				</u-radio-group>
				<view class="sub">
					<u-button size="mini" @click="submit()">确定</u-button>
				</view>
			</view>
		</u-popup>
	</view>
</template>


<script>
	var that
	const app = getApp()
	export default {
		data() {
			return {
				showPay: false,
				payInfo: [{
						name: '微信支付',
						disabled: false,
						type: 1,
					},
					{
						name: '点券抵扣',
						disabled: false,
						type: 2,
					},
				],
				payType:'',
				voucher:0,
				bill:'',
				project_config:''
			}
		},
		onShareAppMessage() {

		},
		methods: {
			viewPayType(){
				that.showPay = true
				if(parseInt(that.voucher) < (parseInt(that.total)*that.project_config.rate)){
					that.payInfo[1].disabled = true
					that.payInfo[1].name = '点券不足'
				}
			},
			radioChange(e) {
				that.payType = e
			},
			submit(){
				if (!that.payType) {
					uni.showToast({
						title: '请选择支付方式',
						icon: 'none'
					})
					return;
				}
				uni.showLoading()
				that.$u.post('bill/pay', {id:that.id,api_token:app.globalData.api_token,payType:that.payType}).then(res => {
					console.log(res)
					that.showPay=false
					uni.showToast({
						title:res.msg,
						icon:'none'
					})
					if(that.payType == 2){
						that.getBill()
						that.getWallet()
						return
					}
					uni.requestPayment({
					    provider: 'wxpay',
					    timeStamp: res.payInfo.timeStamp,
					    nonceStr: res.payInfo.nonceStr,
					    package: res.payInfo.package,
					    signType: res.payInfo.signType,
					    paySign: res.payInfo.paySign,
					    success: function (res) {
					        that.getBill()
							that.getWallet()
					    },
					    fail: function (err) {
					        console.log('fail:' + JSON.stringify(err));
					    }
					});
				}).catch(res=>{
					uni.showToast({
						title:res.msg,
						icon:'none'
					})
				})
			},
			getConfig(){
				that.$u.get('project/config', {}).then(res => {
					that.project_config = res
				})
			},
			getBill(){
				that.$u.get('allBill/detail', {id:that.id,api_token:app.globalData.api_token}).then(res => {
					that.bill = res.data
				})
			},
			getWallet(){
				that.$u.get('wallet', {api_token:app.globalData.api_token}).then(res => {
					that.voucher = res.voucher
				})
			}
		},
		onLoad(e) {
			that = this
			that.id = e.id
		},
		onShow() {
			uni.setStorage({ 
				key: 'url',
				data: '/pages/bill/detail?id='+that.id,
				success: function() {
					app.isLogin().then(function() {
						app.checkLogin()
						that.getConfig()
						that.getBill()
						that.getWallet()
					})
				}
			})
		},
	}
</script>

<style lang="scss">
	@import "uview-ui/index.scss";
	.title{
		text-align: center;
		line-height: 100rpx;
		font-size: 36rpx;
		font-weight: bold;
	}
	.price{
		line-height: 100rpx;
		color: red;
		font-weight: bold;
		padding-left: 20rpx;
	}
	.pay{
		text-align: center;
	}
	.pay_info{
		padding:20rpx;
	}
	.pay_info .u-radio{
		width: 100%!important;
	}
	.pay_info .sub{
		margin:20rpx 0;
		text-align: center;
	}
	
</style>
